// @use "StyleVariable" as StyleVariable;

:root {
    --primary-color: #003C43;
    --secondary-color: #135D66;
    --third-color: #77B0AA;
}

nav {
    background-color: var(--primary-color);
    color: white;
    padding: 30px 40px;
    font-size: 2vw;
    font-weight: 800;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

li {
    margin-right: 20px;
}

li button {
    font-size: 20px;
    padding: 10px 30px;
    border-radius: 20px;
    background-color: rgba(255, 0, 0, 0.759);
    color: white;
    font-weight: bolder;
    border: none;
    text-align: center;
    cursor: pointer;
}

li button:hover {
    background-color: #fff;
    color: rgb(255, 0, 0);
}

a {
    color: white;
    text-decoration: none;
}

a:hover {
    color: var(--third-color);
}

@media screen and (max-width: 768px) {
    li {
        font-size: 3vw;
    }
}